@import url(https://fonts.googleapis.com/css?family=Lato|Raleway|Space+Mono|Shadows+Into+Light);
/* proposed color schema: palette from astronaut image */
/* 
   primary-dark-green:  #16515C
   primary-dark-grey: #203139
   secondary-grey: #656E74
   secondary-red: #D77579
*/
.has-vertically-aligned-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

* {
  box-sizing: border-box;
}

html {
  font-family: 'Raleway', Lato, Tahoma, sans-serif;
  width: 100%;
  min-height: 100%;
  background-color: #203139;
}

.footer {
  bottom: 0;
  width: 100%;
  padding: 0;
  height: 8em;
  line-height: 25px;
  text-align: center;
  /* background: rgb(108, 31, 63); */
  background: #16515C;
  color: azure;
  padding-top: 2rem;
}

.footer a {
  padding: 0rem 1rem 1rem 1rem;
  color: azure
}

.footer a i {
  font-size: 1.5rem;
  padding: 0px 10px;
}

.footer a .nav-item {
    color: azure
}


::-webkit-scrollbar {
  width: 8px;
  background-color: rgba(245, 245, 245, 0);
}

::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgb(108, 31, 63);
}

strong {
  color: orange;
}

@media (max-width: 599px) {
  .footer {
    padding: 2vmin 20vmin;
  }
}
.App {
  margin-bottom: 4vmin;
}

.main-content {
  padding-top: 2rem;
  color: #16515C;
  font-family: 'Raleway', sans-serif;
  background-image: url(/dev/static/media/NA.3a644d94.png);
  background-position: 105% -5%;
  background-repeat: no-repeat;
  background-blend-mode: luminosity;
  background-attachment: fixed;
  min-height: 100vh;
}

@media (max-width: 599px) {
  .main-content {
    margin-right: 10vmin;
    margin-left: 10vmin;
    background-position: center;
  }
}

.content h1, .content h2, .content h3, .content h4, .content h5, .content h6 {
  color: orange;
  font-weight: 600;
  line-height: 1.125;
}

.content table td, .content table th {
  border: 1px solid azure;
  border-width: 0 0 1px;
  padding: .5em .75em;
  vertical-align: top;
}

.content table thead td, .content table thead th {
    border-width: 0 0 2px;
    color: orange;
    border-color: orange;
}

a {
  color:orange;
}

a:hover {
  color: rgb(108, 31, 63);
}

@media(min-width: 599px) {

  button:hover {
    background-color: rgb(108, 31, 63) !important;
  }
}
  
.header-wrapper {
  margin-bottom: 8vmin;
}

button:focus {
  box-shadow: 0 0 0 0.04em rgba(220, 50, 121, 0.25) !important;
}
.LanguageSwitch {
    position: fixed;
    left: 10px;
    bottom: 10px;
    z-index: 100;
}

.SwitchButton {
    color: azure;
    background-color: #16515C;
    border: none;
    outline: none;
    cursor: pointer;
    box-shadow: 0 5px 20px rgba(14, 25, 80, 0.3);
}

.HomeButton {
    position: fixed;
    right: 10px;
    bottom: 10px;
    z-index: 100;
}

.BackToTop {
    color: azure;
    background-color: #16515C;
    border: none;
    outline: none;
    cursor: pointer;
    box-shadow: 0 5px 20px rgba(14, 25, 80, 0.3);
}

.header-wrapper {
    padding-bottom: 30px;
  }
  
.hero {
/* background-image: url('../img/astronaut.png'); */
background-image: url(/dev/static/media/astronaut.a7cab7f0.png);
/* background-image: url(https://cdn.pixabay.com/photo/2019/04/06/06/44/astronaut-4106766_1280.jpg); */
/* background: linear-gradient(rgba(31, 33, 108, 0.65(), originally)rgba(31, 33, 108, 0.65()), originally)rgba(0, 0, 0, 0.55) url("https://cdn.pixabay.com/photo/2019/04/06/06/44/astronaut-4106766_1280.jpg") no-repeat; */
background: linear-gradient(rgba(31, 77, 108, 0.65), rgba(31, 77, 108, 0.65)), rgba(0, 0, 0, 0.55) url(/dev/static/media/astronaut.a7cab7f0.png) no-repeat;
background-attachment: fixed;
background-size: cover;
/* color 2: white (originally)*/
/* color: rgb(162, 196, 247); */
color: #16515C;
box-shadow: inset 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
font-family: 'Raleway', sans-serif;
background-position: center;
}

.hero .title {
font-family: 'Space Mono', sans-serif;
font-size: 4rem;
/* color: #d1f8f8; */
color: orange;
}

.hero .subtitle {
padding: 5px;
/* color 1: whitesmoke (originally)*/
/* color: rgb(78, 212, 194); */
color: azure;
}

.hero .profession {
padding-top: 50px;
font-size: 1.7rem;
text-transform: uppercase;
}

.hero .hero-foot {
height: 50px;
/* color 2: white (originally)*/
/* background: rgb(162, 196, 247); */
background: azure;
box-shadow: 0 2px 15px rgba(0, 0, 0, 0.082);
}

@media (min-width: 599px) {
.hero .up-on-desktop {
    position: relative;
    top: -20vmin;
}
.hero .down-on-desktop {
    position: relative;
    top: 20vmin;
}
.hero-body {
    height: 100vmin;
    }
}

@media (max-width: 599px) {
    .hero .hero-foot {
        display: none;
    }
    .hero {
        height: 100vmax;
    }
}

.hero .hero-foot .hero-foot--wrapper {
margin: 7px auto;
height: 50px;
}

.hero .hero-foot .hero-foot--wrapper .hero-menu-desktop {
line-height: 38px;
font-weight: 600;
/* text-transform: lowercase; */
letter-spacing: 1px;
font-size: 1rem;
}

.hero .hero-foot .hero-foot--wrapper .hero-menu-desktop ul li {
display: inline-block;
padding-right: 15px;
padding-left: 15px;
}

.hero .hero-foot .hero-foot--wrapper .hero-menu-desktop ul li.is-active a {
    /* color 3: #1f2c6c (originally) */
    border-bottom: 2px solid rgb(108, 31, 63);
}

.hero .hero-foot .hero-foot--wrapper .hero-menu-desktop ul li a {
    /* color 3: #1f2c6c (originally) */
    color: rgb(108, 31, 63);
}

.hero .hero-foot .hero-foot--wrapper .hero-menu-desktop ul li a:hover {
    /* color 3: #1f2c6c (originally) */
    color: rgb(108, 31, 63);
transition: all .2s ease-in-out;
    /* color 3: #1f2c6c (originally) */
border-bottom: 2px solid rgb(108, 31, 63);
}
.navbar-burger, .navbar-item {
    color: azure
}

.navbar-burger, .navbar-menu {
    position: fixed;
}
.navbar-menu {
  background-color:  #16515C;
}

 .light-logo-shield {
    display: flex;
    position: relative;
    top: -4vmin;
    justify-content: center;
    height: 16vmin;
 }

 .light-logo-shield img {
   height: inherit;
   border-radius: 0 0 30% 30%;
   background-color: azure;
 }

/* The sticky class is added 
 to the navbar with JS when it
 reaches its scroll position */
.sticky-top {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
}

.sticky-space {
  padding-top: 10vmin;
}





