}

.main-content .section-light.skills {
  padding: 100px;
}

.main-content .section-light.skills h1 {
  padding: 10px;
}

.image-centered {
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.main-text, .personal-claim, .about-links {
  color: azure;
  font-size: 120%;
}

.personal-claim {
  /* background-color: rgb(108, 31, 63); */
