  font-family: 'Shadows Into Light', Lato, Tahoma, sans-serif;
  font-size: 150%;
  letter-spacing: 0.1em;
}

.about-links {
  margin-top: 2vmin;
}

.section-subtitle {
  color: orange;
  text-align: center;
}

progress[value]::-webkit-progress-value {
  background-color: orange;
}

.skill-label {
  color: azure;
}

hr.custom-hr {
  border: 0;
  height: 0;
  box-shadow: 0 0 10px 1px orange;
  width: 80%;
  margin: 20vmin;
}

hr.custom-hr:after {
  content: "\00a0";
}
  
@media (max-width: 599px) {
  .About, .AboutAndSkills {
    margin-bottom: 40vmin;
  }

  .main-content .section-light.about-me .about-links {
    text-align: center;
  }
  .main-content .section-light.about-me .right-image img {
    margin: auto;
  }
  .main-content .section-light.skills {
    padding: 50px 20px 40px 20px;
    padding-bottom: 20px;
  }
  hr.custom-hr {
    display: none
  }
  .has-image-centered {
    margin-bottom: 20vmin
  }
  .main-content .section-light.contact {
