}

.point-to-button {
  animation: updown 2s ease infinite;
  font-size: 10vmin;
  text-align: center;
  width: 100%;
  color: orange !important;
}

@keyframes updown {
  0% {
    transform: translateY(-26%);
  }
  50% {
    transform: translateY(-4%);
  }
  100% {
    transform: translateY(-26%);
  }
}

.download-button-area {
  display: flex;
  flex-flow: column;
  margin: auto;
}

.point-icon-area {
  display: inline-block;
  text-align: center;
}
